import React from 'react'
import { Layout, Typography } from 'antd';
import Sidebar from './Sidebar';
import axios from "axios";
const { Title } = Typography;
const { Header, Content, Footer } = Layout;


class ShopDashBoard extends React.Component {
  state = {
    collapsed: false,
  };

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  componentDidMount() {
    const token = localStorage.getItem('token');
    axios.post('/auth/verifyShop', { token })
      .then(res => {
        if (!res.data.success) {
          this.props.history.push('/shops/login')
        }
      })
      .catch(err => {
        console.log(err)
        this.props.history.push('/shops/login')
      })

  }

  render() {
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Sidebar />
        <Layout>
          <Header style={{ background: '#fff', padding: '6px', margin: '16px', textAlign: 'center' }} >
            <Title level={2}>
              DashBoard
            </Title>
          </Header>
          <Content style={{ margin: '0 16px' }}>
            <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>LiqrCart Shop DashBoard</div>
          </Content>
          <Footer style={{ textAlign: 'center' }}>LiqrCart 2020 Created by Rinnegan Technologies</Footer>
        </Layout>
      </Layout>
    );
  }
}

export default ShopDashBoard;