import React, { Component } from "react";
import axios from 'axios';
import { Typography, Button, Descriptions } from 'antd';
import { withRouter } from 'react-router-dom';
import Header from './Header';
import Navbar from './Navbar';

class Confirm extends Component {
  state = {
    order: {}
  }
  componentDidMount() {
    const token = localStorage.getItem('token');
    axios.post('/auth/verify', { token })
      .then(res => {
        if (res.data.success === false) {
          this.props.history.push('/login')
        }
      });
    // this work is now done in cart component due to cod mode
    // let cart = JSON.parse(localStorage.getItem('cart'));
    // cart.products = [];
    // cart.shopID = '';
    // localStorage.setItem('cart', JSON.stringify(cart));
    let id = "";
    if (this.props.location.hash === '') {
      id = localStorage.getItem('orderID');
    } else {
      // localStorage.setItem('orderID', this.props.location.hash.split('#')[1]);
      // id = localStorage.getItem('orderID');
      if(!id) {
        console.log('Ignore the messages regarding delivery')
      }
    }
    axios.post(`/delivery/customer/${id}`)
      .then(res => {
        this.setState({ order: res.data.order[0] })
      })
  }

  handleRecieve = e => {
    axios.post('/delivery/recieve', { id: this.state.order._id })
      .then(res => {
        localStorage.setItem('orderID', '');
        this.props.history.push('/')
      })
  }


  render() {
    const { order } = this.state;
    return (
      <div>
        <Header />
        <div className="center ">
          {localStorage.getItem('orderID') !== "" ? (<div key={order._id} ><Typography.Title level={4}>Keep patience your order will arrive soon.</Typography.Title>

            <div key={order._id} className="center confirm">
              <Descriptions title="Order Info" layout="vertical" bordered>
                <Descriptions.Item label="Order Id">{order._id}</Descriptions.Item>
                <Descriptions.Item label="Order Address">{order.to}</Descriptions.Item>
                <Descriptions.Item label="Payment">{order.paymentStatus !== 'pending' ? 'Done': 'Pending'}</Descriptions.Item>
                <Descriptions.Item label="Order Status">{order.pickedFromShop === true ? "Shipped" : "To be picked from shop."}</Descriptions.Item>

              </Descriptions>
              <div style={{ margin: '16px' }}></div>
              {order.delivered === true ? "Recieved" : (<Button style={{backgroundColor: 'green', color: 'white'}} name={order._id} onClick={this.handleRecieve}>  Click when recieve. </Button>)}
            </div></div>) : <Typography.Title level={4}>No Order.</Typography.Title>}
        </div>
        <Navbar />
      </div>
    );
  }
}

export default withRouter(Confirm);

