import React from "react";
import { Link, withRouter } from "react-router-dom";
import { Layout, Menu, Icon } from "antd";
const { Sider } = Layout;

class Sidebar extends React.Component {
  state = {
    collapsed: true,
  };

  onCollapse = (collapsed) => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  render() {
    return (
      <Sider
        collapsible
        collapsed={this.state.collapsed}
        onCollapse={this.onCollapse}
      >
        <div className="logo" />
        <Menu theme="dark" mode="inline">
          <Menu.Item key="1">
            <Link to="/captain/orders">
              <Icon type="shopping-cart" />
              <span>Order</span>
            </Link>
          </Menu.Item>
          <Menu.Item key="2">
            <Link to="/captain/shops">
              <Icon type="shop" />
              <span>Shops</span>
            </Link>
          </Menu.Item>

          <Menu.Item key="sub2">
            <span>
              <Link to="/captain/category">
                <Icon type="unordered-list" />
                <span>Categories</span>
              </Link>
            </span>
          </Menu.Item>
          <Menu.Item key="sub4">
            <Link to="/captain/brands">
              <Icon type="apple" />
              <span>Brands</span>
            </Link>
          </Menu.Item>

          <Menu.Item key="9">
            <Link to="/captain/stats">
              <Icon type="line-chart" />
              <span>Stats</span>
            </Link>
          </Menu.Item>
          <Menu.Item key="sub5">
            <Link to="/logout">
              <Icon type="logout" />
              <span>Logout</span>
            </Link>
          </Menu.Item>
        </Menu>
      </Sider>
    );
  }
}

export default withRouter(Sidebar);
