import React from 'react'
import { Layout, Typography, Modal, Button, Descriptions, Row } from 'antd';
import axios from 'axios'
import Sidebar from './Sidebar';
import AddShop from './AddShop'
const { Title } = Typography;
const { Header, Content, Footer } = Layout;


class Shops extends React.Component {
  state = {
    visible: false,
    shops: []
  };

  componentDidMount() {

    const token = localStorage.getItem('token');
    axios.post('/auth/verifyAdmin', { token })
      .then(res => {
        if (!res.data.success) {
          this.props.history.push('/login')
        }
      })
      .catch(err => {
        console.log(err)
        this.props.history.push('/login')
      })

    axios.get('/shop/allShops')
      .then(res => {
        this.setState({ shops: res.data.shops })
      }).catch(err => console.log(err))
  }


  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = e => {
    this.setState({
      visible: false,
    });
  };


  render() {
    const { shops } = this.state
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Sidebar />
        <Layout>
          <Header style={{ background: '#fff', padding: '6px', margin: '16px', textAlign: 'center' }} >
            <Title level={2}>
              Shops
            </Title>
          </Header>
          <Content style={{ margin: '0 16px' }}>
            <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
              <div>
                <Row type="flex" justify="end">
                  <Button type="primary" onClick={this.showModal} style={{ marginRight: '12px' }}>
                    Add New Shop
         </Button>
                </Row>
                <Modal
                  title="Enter New Shop Details"
                  visible={this.state.visible}
                  onCancel={this.handleCancel}
                  onOk={this.handleOk}
                >
                  <AddShop />
                </Modal>
              </div>
              {shops.map(shop => (
                <div key={shop._id} style={shopInfoStyles}>
                  <Descriptions title="Shop Info" layout="vertical" >
                    <Descriptions.Item label="ShopName">{shop.name}</Descriptions.Item>
                    <Descriptions.Item label="Telephone">{shop.phone}</Descriptions.Item>
                    <Descriptions.Item label="Owner">{shop.admin}</Descriptions.Item>
                    <Descriptions.Item label="email">{shop.email}</Descriptions.Item>
                    <Descriptions.Item label="Address">
                      {shop.address}
                    </Descriptions.Item>
                  </Descriptions>
                </div>
              ))}
            </div>
          </Content>
          <Footer style={{ textAlign: 'center' }}>LiqrCart 2020 Created by Rinnegan Technologies</Footer>
        </Layout>
      </Layout>
    );
  }
}

export default Shops;

const shopInfoStyles = {
  padding: '12px',
  margin: '6px',
  border: '0.5px solid grey',
  borderRadius: '5px'
}