import React, { Component } from "react";
import { Input, Button, notification } from 'antd';
import { Link, withRouter } from 'react-router-dom';
import axios from 'axios';
import { Animated } from "react-animated-css";

class ShopLogin extends Component {
  state = {
    email: '',
    password: '',
    type: 'shop'
  }

  handleChange = (e) => {
    e.preventDefault();
    this.setState({ [e.target.name]: e.target.value })
  }

  handleLogin = (e) => {
    e.preventDefault();
    axios.post('/auth/login', {
      email: this.state.email,
      password: this.state.password,
      type: this.state.type
    })
      .then(res => {
        if (res.data.loggedIn) {
          localStorage.setItem('token', res.data.token);
          localStorage.setItem('user', JSON.stringify(res.data.user));
          this.props.history.push('/shops/dashboard');
        } else {
          notification.open({
            message: 'Something went wrong',
            description: 'Please check your credentials.',
            onClick: () => {
              console.log('Notification Clicked!');
            },
          });
        }
      })
  }

  componentDidMount() {
    const token = localStorage.getItem('token');
    axios.post('/auth/verify', { token })
      .then(res => {
        if (res.data.success === true) {
          this.props.history.push('/shops/dashboard')
        }
      });
  }

  render() {
    return (
      <Animated animationIn="rotateInDownRight" animationOut="zoomOutDown" animationInDuration={2000} animationOutDuration={1000} isVisible={true}>
        <div className="login-container">
          <div className="login">
            <Animated animationIn="slideInLeft" animationOut="zoomOutDown" animationInDuration={2500} animationOutDuration={1000} isVisible={true}>
              <h1>Login to LiqrCart.</h1>
            </Animated>
            <Animated animationIn="slideInRight" animationOut="zoomOutDown" animationInDuration={2500} animationOutDuration={1000} isVisible={true}>
              <Input placeholder="Email or Phone" name="email" onChange={this.handleChange} />
              <br /> <br />
            </Animated>
            <Animated animationIn="slideInLeft" animationOut="zoomOutDown" animationInDuration={2500} animationOutDuration={1000} isVisible={true}>
              <Input.Password placeholder="Password" name="password" onChange={this.handleChange} />
              <br /> <br />
            </Animated>
            <Button type="primary" size="large" onClick={this.handleLogin}>Login</Button>
            <br /> <br />
            <p><Link to="/signup">Not have an Account? Sign Up.</Link></p>        </div>
        </div>
      </Animated>
    );
  }
}

export default withRouter(ShopLogin);
