import React, { Component } from "react";
import axios from "axios";
import Navbar from './Navbar';
import { Button, Descriptions, Tag, Select, Typography, notification } from "antd";
const { Option } = Select;
// const dummmy = require('../../img/dummydrink.jpg')

class Product extends Component {
  state = {
    product: {
      _id: '',
      price: '',
      quantity: 1
    },
    size: '',
    quantity: 1,
  }
  componentDidMount() {
    axios.get('/product/productsByID/' + this.props.match.params.id)
      .then(res => {
        this.setState({ product: res.data.product })
      })
  }
  handleQuantityChange = value => {
    if(this.state.quantity<3){
      this.setState({quantity:value})
    }
  }
  handleChange = (value) => {
    this.setState({ size: value });
  }
  // handleAddNOP = () => {
  //   this.setState(prevState => {
  //     let count = (prevState.nop < 3) ? prevState.nop + 1 : prevState.nop;
  //     return {
  //       nop: count
  //     }
  //   })
  // }
  // handleSubNOP = () => {
  //   this.setState(prevState => {
  //     let count = (prevState.nop < 2) ? prevState.nop : prevState.nop - 1;
  //     return {
  //       nop: count
  //     }
  //   })
  // }
  addToCart = () => {
    if(!this.state.size) {
      notification.open({description: 'Please select a size.'});
      return;
    }
    const prices = this.state.product.price
    const { size } = this.state;
    let price;
    if (size === 'q')
      price = prices.q
    if (size === 'h')
      price = prices.h
    if (size === 'f')
      price = prices.f


    let product = {
      _id: this.state.product._id,
      size: this.state.size,
      name: this.state.product.name,
      price: price,
      no: this.state.quantity
    }

    let stringified = localStorage.getItem('cart');
    let cart = JSON.parse(stringified);

    if (cart.shopID !== '') {
      if (cart.shopID === this.state.product.shopID) {
        if (cart.products.length < 3) {
          cart.products.map((p, index) => {
            if (p._id === product._id && p.size === product.size) {
              cart.products[index].no = parseInt(cart.products[index].no) +  1;
            } else {
              cart.products.push(product);
            }
            // Just returning to get rid of warning from react
      return p;
          })
        } else {
          notification.open({description: "cart is full"})
        }
      } else {
        cart.shopID = this.state.product.shopID;
        cart.products = [product]
      }
    } else {
      cart.shopID = this.state.product.shopID;
      cart.products.push(product);
    }


    localStorage.setItem('cart', JSON.stringify(cart));
    this.props.history.push('/cart')
  }
  render() {
    const { product } = this.state;
    let prices = product.price;
    const {q,h,f} = product.quantity;
    // let quantity = product.quantity;
    return (
      <div className="product center">
        <br />
        <Typography.Title>Product Info</Typography.Title>
        <div className="product-info">
        <Descriptions bordered >
          <Descriptions.Item label="Name">{product.name}</Descriptions.Item>
          <Descriptions.Item label="Brand">{product.brand}</Descriptions.Item>
          <Descriptions.Item label="price">
            <Tag color="green">Quater : &#8377;{prices.q}</Tag>
            <Tag color="gold">Half : &#8377;{prices.h}</Tag>
            <Tag color="red">Full : &#8377;{prices.f}</Tag>
          </Descriptions.Item>
          {/* <Descriptions.Item label="stock">
            <Tag color="green">Quater : {quantity.q}</Tag>
            <Tag color="gold">Half : {quantity.h}</Tag>
            <Tag color="red">Full : {quantity.f}</Tag>
          </Descriptions.Item> */}
          </Descriptions> 
        </div>
        <br />
        <div style={{ textAlign: 'center' }}>
          <Typography.Text>Select size : </Typography.Text>
          <Select defaultValue="q" style={{ width: 120, margin: '6px' }} onChange={this.handleChange}>
            <Option disabled={q<0 ? true: false} value="q">Quater</Option>
            <Option disabled={h<0 ? true: false} value="h">Half</Option>
            <Option disabled={f<0 ? true: false} value="f">Full</Option>
          </Select>
          <br />
          <br />

          <Typography.Text>Select quantity : </Typography.Text>
          <Select defaultValue="1" style={{ width: 120, margin: '6px' }} onChange={this.handleQuantityChange}>
            <Option value="1">1</Option>
            <Option value="2">2</Option>
            <Option value="3">3</Option>
          </Select>
          <br />
          <br />
          <Button onClick={this.addToCart} size="large" icon="shopping-cart" style={{ margin: '6px', background: 'black', color: 'white' }}>Add to cart</Button>
        </div>
        <Navbar />
      </div >
    );
  }
}

export default Product;


