import React from 'react'
import { Select, Input, InputNumber, Button, notification } from 'antd';
import axios from "axios";
const { Option } = Select;

class AddProduct extends React.Component {
  state = {
    collapsed: false,
    categories: [],
    brands: [],
    product: {
      name: '',
      brand: '',
      category: '',
      price: { q: 0, h: 0, f: 0 },
      quantity: { q: 0, h: 0, f: 0 }
    }
  };

  componentDidMount() {
    const token = localStorage.getItem('token');
    axios.post('/auth/verifyShop', { token })
      .then(res => {
        if (!res.data.success) {
          this.props.history.push('/shops/login')
        }
      })
      .catch(err => {
        console.log(err)
        this.props.history.push('/shops/login')
      })

    axios
      .get("/category/allCategories")
      .then(res => {
        this.setState({ categories: res.data.categories })
      })
      .catch(err => console.error(err));

    axios
      .get("/brand/allBrands")
      .then(res => {
        this.setState({ brands: res.data.brands })
      })
      .catch(err => console.error(err));
  }

  handleChange = e => {
    console.log(e.target.value)
    let s = this.state;
    s.product.name = e.target.value
    this.setState(s)
  }


  handleCategorySelect = (cat) => {
    this.setState(prevState => {
      prevState.product.category = cat;
      return {
        ...prevState,
      }
    })
  }

  handleBrandSelect = (brand) => {
    this.setState(prevState => {
      prevState.product.brand = brand;
      return {
        ...prevState,
      }
    })
  }

  handleSubmit = (e) => {
    e.preventDefault();
  }


  handlePrice = (kind, value) => {
    this.setState((prevState) => {
      switch (kind) {
        case 'q': prevState.product.price.q = value;
          break;
        case 'h': prevState.product.price.h = value;
          break;
        case 'f': prevState.product.price.f = value;
          break;
        default:
          break;
      }
      return {
        ...prevState
      }
    })
  }

  handleQuantity = (kind, value) => {
    this.setState((prevState) => {
      switch (kind) {
        case 'q': prevState.product.quantity.q = value;
          break;
        case 'h': prevState.product.quantity.h = value;
          break;
        case 'f': prevState.product.quantity.f = value;
          break;
        default:
          break;
      }
      return {
        ...prevState
      }
    })
  }

  handleAddProduct = (e) => {
    e.preventDefault();
    let { name, brand, category, quantity, price } = this.state.product;
    if (!name || !brand || !category) {
      this.openNotification('Please check your inputs');
      return;
    }
    const { q, h, f } = quantity;
    if (!q || !h || !f) {
      this.openNotification('Please check your quanitities.');
      return;
    }
    if (!price.q || !price.h || !price.f) {
      this.openNotification('Please check your prices');
      return;
    }
    let user = JSON.parse(localStorage.getItem('user'))

    axios.post('/product/add', {
      accessToken: localStorage.getItem('accessToken'),
      name,
      brand,
      category,
      quantity,
      price,
      user,
      type: 'shop'
    })
      .then(res => {
        if (res.data.success) {
          this.openNotification('product added successfully.');
          this.props.history.push('/shops/Stock')
        } else {
          this.openNotification('something went wrong.')
        }
      })
  }


  openNotification = (desc) => {
    notification.open({
      message: 'Notification',
      description:
        desc,
      onClick: () => {
        console.log('Notification Clicked!');
      },
    });
  };

  render() {
    return (
      <div>
        <form onSubmit={this.handleSubmit}>
          <label>Name</label>
          <Input placeholder="Product Name" onChange={this.handleChange} />
          <br /><br />
          <label>Category</label>
          <br />
          <Select
            onChange={this.handleCategorySelect}
            showSearch
            style={{ width: 200, margin: 6 }}
            placeholder="Select a Category"
            optionFilterProp="children"
            filterOption={(input, option) =>
              option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
          >
            {this.state.categories.map(cat => <Option value={cat.name} key={cat._id}>{cat.name}</Option>)}
          </Select>
          <br /><br />
          <label>Brand</label>
          <br />
          <Select
            onChange={this.handleBrandSelect}
            showSearch
            style={{ width: 200, margin: 6 }}
            placeholder="Select a Brand"
            optionFilterProp="children"
            filterOption={(input, option) =>
              option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
          >
            {this.state.brands.map(brand => <Option value={brand.name} key={brand._id}>{brand.name}</Option>)}
          </Select>
          <br /><br />
          <label>Price</label>
          <br />
          <InputNumber placeholder="Quater" min={1} name="q" onChange={(value) => this.handlePrice('q', value)} style={{ margin: 6 }} />
          <InputNumber placeholder="Half" min={1} name="h" onChange={(value) => this.handlePrice('h', value)} style={{ margin: 6 }} />
          <InputNumber placeholder="Full" min={1} name="f" onChange={(value) => this.handlePrice('f', value)} style={{ margin: 6 }} />
          <br /><br />
          <label>Quantity</label>
          <br />
          <InputNumber placeholder="Quater" min={1} onChange={(value) => this.handleQuantity('q', value)} style={{ margin: 6 }} />
          <InputNumber placeholder="Half" min={1} onChange={(value) => this.handleQuantity('h', value)} style={{ margin: 6 }} />
          <InputNumber placeholder="Full" min={1} onChange={(value) => this.handleQuantity('f', value)} style={{ margin: 6 }} />
          <br /><br />
          <Button size="large" type="primary" onClick={this.handleAddProduct}> Add Product</Button>
        </form>
      </div>

    );
  }
}

export default AddProduct;