import React from "react";
import {
  Layout,
  Typography,
  Modal,
  Input,
  Row,
  Button,
  notification,
} from "antd";
import Sidebar from "./Sidebar";
import axios from "axios";
import FileUpload from "./FileUpload";
const { Title } = Typography;
const { Header, Content, Footer } = Layout;

class Brands extends React.Component {
  state = {
    collapsed: false,
    visible: false,
    newBrandName: "",
    image: "",
    brands: [],
  };

  setImage = (image) => {
    this.setState({ image });
  };

  componentDidMount() {
    const token = localStorage.getItem("token");
    axios
      .post("/auth/verifyAdmin", { token })
      .then((res) => {
        if (!res.data.success) {
          this.props.history.push("/login");
        }
      })
      .catch((err) => {
        console.log(err);
        this.props.history.push("/login");
      });

    axios.get("/brand/allBrands").then(({ data }) => {
      this.setState({ brands: data.brands });
    });
  }

  onCollapse = (collapsed) => {
    console.log(collapsed);
    this.setState({ collapsed });
  };
  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e) => {
    this.setState({
      visible: false,
    });
    axios
      .post("/brand/add", {
        name: this.state.newBrandName.trim().toLowerCase(),
        image: this.state.image,
      })
      .then(({ data }) => {
        if (data.success) {
          console.log(data.brand);
          this.setState({ categories: [data.brand, ...this.state.brands] });
          this.openNotification("Brand Created.");
        } else {
          this.openNotification("Brand Could not be Created.");
        }
      });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleDelete = (id) => {
    axios.post("/brand/delete", { brandID: id }).then((res) => {
      if (res.data.success) {
        let newCats = this.state.brands.filter((cat) => {
          return cat._id !== id;
        });
        this.setState({ brands: newCats });
        this.openNotification("Brand deleted.");
      } else {
        this.openNotification("Brand Could not be deleted.");
      }
    });
  };

  handleChange = (e) => {
    this.setState({ newBrandName: e.target.value });
  };

  openNotification = (desc) => {
    notification.open({
      message: "Notification",
      description: desc,
      onClick: () => {
        console.log("Notification Clicked!");
      },
    });
  };

  render() {
    const { brands } = this.state;
    return (
      <Layout style={{ minHeight: "100vh" }}>
        <Sidebar />
        <Layout>
          <Header
            style={{
              background: "#fff",
              padding: "6px",
              margin: "16px",
              textAlign: "center",
            }}
          >
            <Title level={3}>Brands</Title>
          </Header>
          <Content style={{ margin: "0 16px" }}>
            <div style={{ padding: 24, background: "#fff", minHeight: 360 }}>
              <Row type="flex" justify="end">
                <Button
                  type="primary"
                  onClick={this.showModal}
                  style={{ marginRight: "12px", marginBottom: "16px" }}
                >
                  Add New Brand
                </Button>
              </Row>
              {brands.map((brand) => (
                <Row
                  type="flex"
                  justify="space-between"
                  style={RowStyles}
                  key={brand._id}
                >
                  <Typography.Text>{brand.name}</Typography.Text>
                  <Button
                    type="danger"
                    size="small"
                    onClick={() => {
                      this.handleDelete(brand._id);
                    }}
                    style={{ marginLeft: "16px" }}
                  >
                    Delete
                  </Button>
                </Row>
              ))}
            </div>

            <div>
              <Modal
                title="Enter Brand Name"
                visible={this.state.visible}
                onCancel={this.handleCancel}
                onOk={this.handleOk}
                okButtonText="ADD"
              >
                <FileUpload setImage={this.setImage} />
                <Input placeholder="Brand Name" onChange={this.handleChange} />
              </Modal>
            </div>
          </Content>
          <Footer style={{ textAlign: "center" }}>
            LiqrCart 2020 Created by Rinnegan Technologies
          </Footer>
        </Layout>
      </Layout>
    );
  }
}

export default Brands;

const RowStyles = {
  border: "0.5px solid grey",
  borderRadius: "5px",
  margin: "12px",
  padding: "12px",
};
