import React, { Component } from "react";
import axios from 'axios';
import { Link, withRouter } from 'react-router-dom';
import { Affix } from 'antd';
const gridSvg = require('../../css/icons/grid.svg')
const userSvg = require('../../css/icons/log-out.svg')
const logo = require('../../img/logo.png')

class Header extends Component {
  componentDidMount() {
    const token = localStorage.getItem('token');
    axios.post('/auth/verify', { token })
      .then(res => {
        if (res.data.success === false) {
          this.props.history.push('/login')
        }
      });
  }
  render() {
    return (
      <Affix offsetTop={0}>
        <div className="header">
          <Link to="/confirm"><span><img src={gridSvg} alt="menu" /></span></Link>
          <Link to="/" style={{ color: 'black' }}><img src={logo} alt="" className="logo" /></Link>
          <span><Link to="/logout" style={{ color: 'black' }}><img src={userSvg} alt="menu" /></Link></span>
        </div>
      </Affix>
    );
  }
}

export default withRouter(Header);

