import React from "react";
import { Layout, Typography, Descriptions, Button, notification } from "antd";
import Sidebar from "./Sidebar";
import axios from "axios";
const { Title } = Typography;
const { Header, Content, Footer } = Layout;

class ShopOrders extends React.Component {
  state = {
    collapsed: false,
    orders: [],
  };

  onCollapse = (collapsed) => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  openNotification = (desc) => {
    notification.open({
      message: "Notification",
      description: desc,
      onClick: () => {
        console.log("Notification Clicked!");
      },
    });
  };

  componentDidMount() {
    const token = localStorage.getItem("token");
    axios
      .post("/auth/verifyShop", { token })
      .then((res) => {
        if (!res.data.success) {
          this.props.history.push("/shops/login");
        }
      })
      .catch((err) => {
        console.log(err);
        this.props.history.push("/shops/login");
      });
    let shop = JSON.parse(localStorage.getItem("user"));
    axios
      .post(`/delivery/order/${shop._id}`)
      .then((res) => {
        this.setState({ orders: res.data.orders.reverse() });
      })
      .catch((err) => {
        console.log(err);
      });
  }

  handleShip = (e) => {
    let shop = JSON.parse(localStorage.getItem("user"));
    e.persist();
    axios.post("/delivery/ship", { id: e.target.name }).then((res) => {
      if (res.data.success) {
        this.openNotification("Product shipped");
        axios.post(`/delivery/order/${shop._id}`).then((res) => {
          this.setState({ orders: res.data.orders.reverse() });
        });
      } else {
        this.openNotification("Something went wrong.");
      }
    });
  };

  render() {
    return (
      <Layout style={{ minHeight: "100vh" }}>
        <Sidebar />
        <Layout>
          <Header
            style={{
              background: "#fff",
              padding: "6px",
              margin: "16px",
              textAlign: "center",
            }}
          >
            <Title level={2}>DashBoard</Title>
          </Header>
          <Content style={{ margin: "0 16px" }}>
            <div style={{ padding: 24, background: "#fff", minHeight: 360 }}>
              LiqrCart Shop Orders
              {this.state.orders.map((order) => (
                <div key={order._id} style={orderInfoStyles}>
                  <Descriptions title="Order Info" layout="horizontal">
                    <Descriptions.Item label="Order Id">
                      {order._id}
                    </Descriptions.Item>
                    <Descriptions.Item label="Order Address">
                      {order.to}
                    </Descriptions.Item>
                    <br />
                    {order.products.map((p) => (
                      <div key={p._id + Math.random()}>
                        <div>
                          <strong>Product Details</strong>
                          <p label="Type">
                            <strong>Name: </strong>
                            {p.name}
                          </p>
                          <p label="Quantity">
                            <strong>Size: </strong>
                            {p.size}
                          </p>
                          <p label="Number">
                            <strong>No.: </strong>
                            {p.no}
                          </p>
                        </div>
                      </div>
                    ))}
                    <Descriptions.Item label="Payment">
                      {order.paymentStatus}
                    </Descriptions.Item>
                    <Descriptions.Item label="Order Status">
                      {order.pickedFromShop === true ? (
                        "Shipped"
                      ) : (
                        <Button
                          type="danger"
                          name={order._id}
                          onClick={this.handleShip}
                        >
                          Ship Products{" "}
                        </Button>
                      )}
                    </Descriptions.Item>
                  </Descriptions>
                </div>
              ))}
            </div>
          </Content>
          <Footer style={{ textAlign: "center" }}>
            LiqrCart 2020 Created by Rinnegan Technologies
          </Footer>
        </Layout>
      </Layout>
    );
  }
}

export default ShopOrders;

const orderInfoStyles = {
  padding: "12px",
  margin: "6px",
  border: "0.5px solid grey",
  borderRadius: "5px",
};
