import React, { Component } from "react";
import { Typography, notification , Button, Input} from 'antd';
import Header from '../Layout/Header'
import axios from "axios"
const clock = require('../../css/icons/clock.svg')
const x = require('../../css/icons/x.svg')
const next = require('../../css/icons/chevron-right.svg')

class Cart extends Component {
  state = {
    user: {},
    cart: { products: [] },
    shop: {},
    products: [],
    to: '',
    check: false,
    delRate: 0,
    total:0
  }
  componentDidMount() {
    let user = JSON.parse(localStorage.getItem('user'));
    let cart = JSON.parse(localStorage.getItem('cart'));
    this.setState({ user: user });
    this.setState({ cart });
    axios.get(`/shop/shopById/${cart.shopID}`)
      .then(res => {
        this.setState({ shop: res.data.shop });
        // this line might give an type error when the cart is empty. we can ignore it
        cart.from =  res.data.shop.address;
        cart.to = this.state.to !== '' ? this.state.to : this.state.user.address;
        localStorage.setItem('cart', JSON.stringify(cart))
        this.setState({ cart });
        let amount = 0;
        let charges = 0;
        this.state.cart.products.map(p => amount += p.price * p.no);
        if(amount< 1000 && amount >0) 
          charges = 50;
        if(amount <1500 && amount >999)
          charges = 75;
        if(amount >= 2000)
          charges = 100;
        this.setState({delRate: charges, total:amount});
      }).catch(err => console.log(err))
    cart.products.map(p => {
      axios.get(`/product/productsByID/${p._id}`)
        .then(res => {
          let newProducts = [...this.state.products, res.data];
          this.setState({ products: newProducts })
        })
        .catch(err => console.log(err));

   
        // just returning to get rid of warning from react
        return p;
    })

  }

  handleRemove = (e) => {
    let { products } = this.state.cart;
    let newProducts = [];
    let pro = products.find(p => p._id === e.target.id && p.size === e.target.name);
    products.map(p => {
      if (p !== pro) {
        newProducts.push(p)
      }
      // Just returning to get rid of warning from react
      return p;
    })
    let newCart = { ...this.state.cart, products: newProducts };

    if (newCart.products.length === 0)
      newCart.shopID = "";
    this.setState({ cart: newCart });
    localStorage.setItem('cart', JSON.stringify(newCart));
  }

  handleToAddresss = (e) => {
    this.setState({to: e.target.value, 
      cart: {...this.state.cart, to:e.target.value}
    });
  }
  showNotification = (desc) => {
    notification.open({
      message: 'Something went wrong',
      description: desc,
      onClick: () => {
        console.log('Notification Clicked!');
      },
    });
  }
  handleOrder = () => {
    if(!this.state.cart.to){
      this.showNotification('Delivery Address cannot br empty.');
      return;
    }
    if ( this.state.cart.products.length < 1){
      this.showNotification('Cart is empty.');
      return;}
      if (this.state.cart.shopID === ''){
        this.showNotification('Try to login again to fix this issue.');
      return;}
    axios.post('/payment/checkout', { amount: this.state.total, cart: this.state.cart })
      .then(res => {
        if (res.data.success) {
          notification.open({
            message: 'Order Placed',
            description: 'We have recieved your order. \n It will arrive soon.',
            onClick: () => {
              console.log('Notification Clicked!');
            },
          });
          // reset the cart details
          this.setState({
            cart: { ...this.state.cart,products: [],shopID: '' },
            shop: {},
            products: [],
            to: '',
            check: false,
            delRate:0,
            total:0
          });
          localStorage.setItem('cart', JSON.stringify({ ...this.state.cart,products: [],shopID: '' }))
          localStorage.setItem('orderID', res.data.orderID);
          // console.log(res.data.response)
          // window.location.href = res.data.response.payment_request.longurl;
        } else {
          this.showNotification(res.data.err);
        }
      })
      .catch(err => {
        notification.open({
          message: 'Something went wrong',
          description: 'We\'ll try to fix this issue soon.',
          onClick: () => {
            console.log('Notification Clicked!');
          },
        });
      })
  }

  // handleRemove = (e) => {
  //   let newProducts = [];
  //   let pro = 
  //   this.state.cart.products.map((p, index) => {
  //     if(p._id !== e.target.id && p.size !==  this.state.cart.products[index].size)  
  //       newProducts.push(p)
  //   });
  //   let newCart = {...this.state.cart,products:newProducts}
  //   this.setState({cart: newCart});
  //   localStorage.setItem('cart', JSON.stringify(this.state.cart))
  // }
  render() {
    // let total = 0;
    // this.state.cart.products.map(p => total += p.price * p.no);
    const {total, delRate} = this.state;
    return (
      <>
        <Header />
      <div className="cart">
        <div  style={{ marginLeft: '24px' }} >
          <Typography.Title >Cart</Typography.Title>
          <div className="est">
            <img src={clock} alt="" />
            <span><Typography.Text> 40-30 MINS estimated dilevery time.</Typography.Text></span>
          </div>
          <div className="yellow">
            <h4>Dilevery At </h4>
            <p>{this.state.to ? this.state.to : this.state.user.address}</p>
            <p>Want To Change Dilevry Location?  <span><Button size="small" onClick={() => this.setState({check: !this.state.check})}>{this.state.check ? 'Done': 'Change'}</Button></span></p>
            {!this.state.check ? '' : (<Input placeholder="Delivery Address" onChange={this.handleToAddresss}/>)}
          </div>
        </div>
        <Typography.Title level={4} style={{ marginLeft: '12px', marginTop: '24px' }}>Your Order</Typography.Title>
    <small style={{margin: 'auto 10px'}}>Plus Rs. {this.state.delRate} delivery charges.</small>
        <div>
          {this.state.cart.products.map(p => (< div key={p._id + p.size} className="order-single" style={orderSingle}>
            <div >
              <p style={{ fontWeight: 'bold', fontSize: '14px', textTransform: 'capitalize' }}>{p.name}</p>
              <p> &#8377; {p.price} per bottle</p>
              <p>Quantity : {p.no}</p>
              <p>Vendor : {this.state.shop.name}</p>
            </div>
            <img src={x} alt='' name={p.size} id={p._id} onClick={this.handleRemove} style={{ justifyContent: 'end' }} />
          </div>
          ))}
        </div>

        <div className="clear"></div>
        <div className='total' onClick={this.handleOrder}>
          <span>Total: &#8377; {total > 0 ? total + delRate : 0 }</span>
          <button> Place Order <img src={next} alt="" /></button>
        </div>
      </div>
      </>
    );
  }
}

export default Cart;

const orderSingle = {
  margin: '12px',
  padding: '6px',
  fontSize: '10px',
  background: '#fff',
  borderRadius: '15px'
}