import React from "react";
import {
  Layout,
  Typography,
  Descriptions,
  Button,
  notification,
  Tabs,
} from "antd";
import Sidebar from "./Sidebar";
import axios from "axios";
const { TabPane } = Tabs;
const { Title } = Typography;
const { Header, Content, Footer } = Layout;

class Orders extends React.Component {
  state = {
    collapsed: false,
    orders: [],
    clients: [],
    detailedOrders: [],
  };

  componentDidMount() {
    const token = localStorage.getItem("token");
    axios
      .post("/auth/verifyAdmin", { token })
      .then((res) => {
        if (!res.data.success) {
          this.props.history.push("/login");
        }
      })
      .catch((err) => {
        console.log(err);
        this.props.history.push("/login");
      });
    axios.get("/delivery/allOrders").then((res) => {
      this.setState({ orders: res.data.orders.reverse() });
      if (res.data.orders.length > 0) {
        this.state.orders.map((order) => {
          axios
            .post(`/client/getById`, { id: order.clientID })
            .then((res) => {
              const { client } = res.data;
              order.client = client;
              this.setState({
                detailedOrders: [...this.state.detailedOrders, order],
              });
            })
            .catch((err) => console.error(err));
          // Just returning to get rid of warning from react
          return order;
        });
      }
    });
  }

  confirmPayment = (orderID) => {
    axios
      .post("/payment/confirm", { orderID })
      .then((res) => {
        if (res.data.success) {
          notification.open({
            title: "Success",
            description: "Payment marked as complete.",
          });
        } else {
          notification.open({
            title: "Error",
            description: res.data.err,
          });
        }
      })
      .catch((err) => {
        console.log(err);
        notification.open({
          title: "Error",
          description: "Server/Network Error. Request Failed",
        });
      });
  };

  onCollapse = (collapsed) => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  callback = (key) => {
    console.log(key);
  };

  render() {
    return (
      <Layout style={{ minHeight: "100vh" }}>
        <Sidebar />
        <Layout>
          <Header
            style={{
              background: "#fff",
              padding: "6px",
              margin: "16px",
              textAlign: "center",
            }}
          >
            <Title level={2}>Orders</Title>
          </Header>
          <Content style={{ margin: "0 16px" }}>
            <div style={{ padding: 24, background: "#fff", minHeight: 360 }}>
              Liqr Cart Orders
              <Tabs defaultActiveKey="1" onChange={this.callback}>
                <TabPane tab="Pending Orders" key="pending">
                  {this.state.detailedOrders.map((order) => {
                    if (order.paymentStatus === "pending") {
                      return (
                        <OrderInfo
                          order={order}
                          key={order._id}
                          confirmPayment={this.confirmPayment}
                        />
                      );
                    }
                    return "";
                  })}
                </TabPane>
                <TabPane tab="Completed orders" key="complete">
                  {this.state.detailedOrders.map((order) => {
                    if (order.paymentStatus === "credit") {
                      return (
                        <OrderInfo
                          order={order}
                          key={order._id}
                          confirmPayment={this.confirmPayment}
                        />
                      );
                    }
                    return "";
                  })}
                </TabPane>
              </Tabs>
            </div>
          </Content>
          <Footer style={{ textAlign: "center" }}>
            LiqrCart 2020 Created by Rinnegan Technologies
          </Footer>
        </Layout>
      </Layout>
    );
  }
}

export default Orders;

function OrderInfo({ order, confirmPayment }) {
  return (
    <div key={order._id} style={orderInfoStyles}>
      <Descriptions title="Order Info" layout="horizontal">
        <Descriptions.Item label="Order Id">{order._id}</Descriptions.Item>
        <Descriptions.Item label="Order Address">{order.to}</Descriptions.Item>
        <br />
        {order.products.map((p) => (
          <div key={p._id + Math.random()}>
            <div>
              <strong>Product Details</strong>
              <p label="Type">
                <strong>Name: </strong>
                {p.name}
              </p>
              <p label="Quantity">
                <strong>Size: </strong>
                {p.size}
              </p>
              <p label="Number">
                <strong>No.: </strong>
                {p.no}
              </p>
            </div>
          </div>
        ))}
        <div>
          <strong>Payment Info: </strong>
          <br />
          <strong>Amount: </strong>
          {order.amount}
          <br />
          <strong>Status: </strong>
          {order.paymentStatus !== "pending" ? "Recieved" : "Pending"}
        </div>

        <Descriptions title="Client Info" layout="horizontal">
          <h3>Client Info</h3>
          <Descriptions.Item label="Name">
            <strong>Name: </strong>
            {order.client.name}
          </Descriptions.Item>
          <br />
          <Descriptions.Item label="Phone">
            <strong>Phone: </strong>
            {order.client.phone}
          </Descriptions.Item>
          <br />
          <Descriptions.Item label="Address">
            <strong>Address: </strong>
            {order.client.address}
          </Descriptions.Item>
          <br />
        </Descriptions>
      </Descriptions>
      {order.paymentStatus === "credit" ? (
        ""
      ) : (
        <Button type="danger" onClick={() => confirmPayment(order._id)}>
          Confirm Payment
        </Button>
      )}
    </div>
  );
}

const orderInfoStyles = {
  padding: "12px",
  margin: "6px",
  border: "0.5px solid grey",
  borderRadius: "5px",
};
