import React, { Component } from "react";
import reactGA from 'react-ga';

import {
  Input,
  Radio,
  Typography,
  Button,
  notification,
  Steps,
  message,
} from "antd";
import axios from "axios";

const { Step } = Steps;
const { Text } = Typography;

class Signup extends Component {
  state = {
    current: 0,
    otp: "",
    name: "",
    gender: "",
    phone: "",
    email: "",
    address: "",
    password: "",
    cpassword: "",
    age: "",
  };

  componentDidMount() {
    reactGA.initialize('UA-166832202-1');
    reactGA.pageview('/register');
  }

  openNotification = (desc) => {
    notification.open({
      message: "Notification",
      description: desc,
      onClick: () => {
        console.log("Notification Clicked!");
      },
    });
  };

  handleSubmit = () => {
    const {
      name,
      gender,
      phone,
      email,
      age,
      address,
      password,
      cpassword,
    } = this.state;
    if (name === "") {
      this.openNotification("Please enter your Name.");
      return;
    }
    if (gender === "") {
      this.openNotification("Please select your gender.");
      return;
    }
    if (email === "") {
      this.openNotification("Please enter your email.");
      return;
    }
    if (age === "") {
      this.openNotification("Please enter your age.");
      return;
    }
    if (age < 18) {
      this.openNotification("You cannot signup Age not legal.");
      return;
    }
    if (address === "") {
      this.openNotification("Please enter your address.");
      return;
    }
    if (phone === "") {
      this.openNotification("Please enter your Phone Number.");
      return;
    }
    if (password === "") {
      this.openNotification("Please enter your Password.");
      return;
    }
    if (cpassword === "") {
      this.openNotification("Please enter your Confirm Password.");
      return;
    }
    if (password !== cpassword) {
      this.openNotification("Passwords do not match.");
      return;
    }

    // Send to server
    axios
      .post("/client/add", {
        name,
        age,
        gender,
        phone,
        email,
        address,
        password,
      })
      .then((res) => {
        if (res.data.success === false) {
          let err = res.data.err
            ? res.data.err
            : "Something went wrong please try again later.";
          this.openNotification(err);
          return;
        } else {
          this.openNotification("Successfully registered")
          localStorage.setItem("token", res.data.user._id);
          this.props.history.push("/login");
        }
      });
  };

  onChange = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };

  sendotp = () => {
    axios.post("/auth/sendotp", { phone: this.state.phone }).then((res) => {
      if (!res.data.success) {
        message.warning("Something went wrong while sending otp.");
        return;
      } else {
        const current = this.state.current + 1;
        this.setState({ current });
      }
    });
  };

  resendotp = () => {
    axios.post("/auth/resendotp", { phone: this.state.phone }).then((res) => {
      if (!res.data.success) {
        message.warning("Something went wrong while resending otp.");
        return;
      }
    });
  };
  verifyotp = () => {
    axios
      .post("/auth/verifyotp", { phone: this.state.phone, otp: this.state.otp })
      .then((res) => {
        if (!res.data.success) {
          message.warning('Invalid Otp');
          return;
        } else {
          
          this.handleSubmit();
        }
      });
  };

  next() {
    if (this.state.phone.length < 10) {
      message.warning("Invalid Phone Number");
      return;
    }
    this.sendotp();
  }

  prev() {
    const current = this.state.current - 1;
    this.setState({ current });
  }

  steps = [
    {
      title: "Register",
      content: (
        <>
          <h1 className="heading">Sign Up to LiqrCart.</h1>
          <Text>Name</Text>
          <Input placeholder="Name" name="name" onChange={this.onChange} />
          <br />
          <br />
          <Text>Phone</Text>
          <Input
            placeholder="Phone"
            type="number"
            name="phone"
            onChange={this.onChange}
          />
          <br />
          <br />
          <Text>Email</Text>
          <Input
            type="email"
            placeholder="email"
            name="email"
            onChange={this.onChange}
          />
          <br />
          <br />
          <Text>Age</Text>
          <Input
            type="number"
            placeholder="Age"
            name="age"
            onChange={this.onChange}
          />
          <br />
          <br />
          <Text>Address</Text>
          <Input
            placeholder="Address"
            name="address"
            onChange={this.onChange}
          />
          <br />
          <br />
          <Text>Gender</Text>
          <br />
          <Radio.Group name="gender" onChange={this.onChange}>
            <Radio value="male">Male</Radio>
            <Radio value="female">Female</Radio>
            <Radio value="others">Other</Radio>
          </Radio.Group>
          <br />
          <br />
          <Text>Password</Text>
          <Input.Password
            placeholder="Password"
            name="password"
            onChange={this.onChange}
          />
          <br />
          <br />
          <Text>Confirm Password</Text>
          <Input.Password
            placeholder="Confirm Password"
            name="cpassword"
            onChange={this.onChange}
          />
          <br />
          <br />
          {/* <Button type="primary" size="large" onClick={this.handleSubmit}>SignUp</Button> */}
        </>
      ),
    },
    {
      title: "Verify",
      content: (
        <>
          <strong>We have sent you an otp</strong>
          <br />
          <br />
          <Input
            onChange={(e) => this.setState({ otp: e.target.value })}
            placeholder="Enter OTP"
          />
          <br />
          <br />
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <Button onClick={this.verifyotp} type="default">
              Verify Otp
            </Button>
            <Button onClick={this.resendotp} type="danger">
              Resend Otp
            </Button>
          </div>
        </>
      ),
    },
  ];

  render() {
    const { current } = this.state;
    return (
      <div className="signup">
        <Steps current={this.state.current}>
          {this.steps.map((item) => (
            <Step key={item.title} title={item.title} />
          ))}
        </Steps>
        <div className="steps-content">{this.steps[current].content}</div>
        <div className="steps-action">
          {current < this.steps.length - 1 && (
            <Button type="primary" onClick={() => this.next()}>
              Next
            </Button>
          )}
          {current === this.steps.length - 1 && (
            <Button type="primary" onClick={this.verifyotp}>
              SignUp
            </Button>
          )}
          {current > 0 && (
            <Button style={{ margin: "0 8px" }} onClick={() => this.prev()}>
              Back
            </Button>
          )}
        </div>
      </div>
    );
  }
}

export default Signup;
