import React from 'react'
import { Layout, Typography, Button, Descriptions, notification } from 'antd';
import axios from 'axios'
import Sidebar from './Sidebar';
const { Title } = Typography;
const { Header, Content, Footer } = Layout;


class ShopStock extends React.Component {
  state = {
    collapsed: false,
    products: []
  };

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  handleDelete = id => {
    axios.post('/product/delete', { type: 'shop', id })
      .then(res => {
        if (res.data.success) {

          this.openNotification('Product Deleted.');
          let prods = this.state.products.filter(p => p._id !== id);
          this.setState({ products: prods })
        } else {
          this.openNotification('Something went wrong.');
        }

      })
  }


  openNotification = (desc) => {
    notification.open({
      message: 'Notification',
      description:
        desc,
      onClick: () => {
        console.log('Notification Clicked!');
      },
    });
  };

  componentDidMount() {

    const token = localStorage.getItem('token');
    axios.post('/auth/verifyShop', { token })
      .then(res => {
        if (!res.data.success) {
          this.props.history.push('/shops/login')
        }
      })
      .catch(err => {
        console.log(err)
        this.props.history.push('/shops/login')
      })


    axios.get('/product/products')
      .then(res => {
        let products: [];
        let shopID = localStorage.getItem('shopID');
        products = res.data.products.filter(product => product._id !== shopID)
        this.setState({ products })
      })
  }


  render() {
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Sidebar />
        <Layout>
          <Header style={{ background: '#fff', padding: '6px', margin: '16px', textAlign: 'center' }} >
            <Title level={2}>
              DashBoard
            </Title>
          </Header>
          <Content style={{ margin: '0 16px' }}>
            <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>LiqrCart Shop Stocks
            {this.state.products.map(product => (
              <div key={product._id} style={productInfoStyles}>
                <Descriptions title="product Info" layout="vertical" >
                  <Descriptions.Item label="productName">{product.name}</Descriptions.Item>
                </Descriptions>
                <Button type="danger" size="small" onClick={() => {
                  this.handleDelete(product._id);
                }} style={{ marginLeft: '16px' }}>Delete</Button>
              </div>
            ))}
            </div>

          </Content>
          <Footer style={{ textAlign: 'center' }}>LiqrCart 2020 Created by Rinnegan Technologies</Footer>
        </Layout>
      </Layout>
    );
  }
}

export default ShopStock;

const productInfoStyles = {
  padding: '12px',
  margin: '6px',
  border: '0.5px solid grey',
  borderRadius: '5px'
}