import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Home from "./components/Layout/Home";
import Confirm from "./components/Layout/Confirm";
import NotFound from "./components/NotFound";
import Login from "./components/Login/Login";
import Logout from "./components/Login/Logout";
import Signup from "./components/Register/Signup";
import Cart from "./components/Cart/Cart";
import DashBoard from "./components/Admin/DashBoard";
import Category from "./components/Admin/Category";
import ShopLogin from "./components/Shop/ShopLogin";
import Shops from "./components/Admin/Shops";
import Stats from "./components/Admin/Stats";
import Orders from "./components/Admin/Orders";
import Brands from "./components/Admin/Brands";
import ShopDashBoard from "./components/Shop/ShopDashBoard";
import ShopOrders from "./components/Shop/ShopOrders";
import ShopStock from "./components/Shop/ShopStock";
import ShopProduct from "./components/Shop/ShopProduct";
import Product from "./components/Layout/Product";
import AllProducts from "./components/Layout/AllProducts";
import AllBrands from "./components/Layout/AllBrands";
import BrandProducts from "./components/Layout/BrandProducts";
import Search from "./components/Layout/Search";
import SearchCategory from "./components/Layout/SearchCategory";
import ForgotPassword from "./components/Login/ForgotPassword";

class App extends React.Component {
  render() {
    return (
      <BrowserRouter>
        <Switch>
          <Route exact path="/" component={Home}></Route>
          <Route path="/login" exact component={Login} />
          <Route path="/logout" exact component={Logout} />
          <Route path="/signup" exact component={Signup} />
          <Route path="/shops/login" exact component={ShopLogin} />
          <Route path="/shops/dashboard" exact component={ShopDashBoard} />
          <Route path="/shops/orders" exact component={ShopOrders} />
          <Route path="/shops/stock" exact component={ShopStock} />
          <Route path="/shops/product" exact component={ShopProduct} />
          <Route path="/cart" exact component={Cart} />
          <Route path="/captain" exact component={DashBoard} />
          <Route path="/captain/category" exact component={Category} />
          <Route path="/captain/shops" exact component={Shops} />
          <Route path="/captain/stats" exact component={Stats} />
          <Route path="/captain/orders" exact component={Orders} />
          <Route path="/captain/brands" exact component={Brands} />
          <Route path="/getproduct/:id" exact component={Product} />
          <Route path="/allproducts" exact component={AllProducts} />
          <Route path="/allbrands" exact component={AllBrands} />
          <Route path="/allbrands/:name" exact component={BrandProducts} />
          <Route path="/search" exact component={Search} />
          <Route path="/search/category" exact component={SearchCategory} />
          <Route path="/confirm" component={Confirm} />
          <Route path="/forgot" component={ForgotPassword} />
          <Route component={NotFound} />
        </Switch>
      </BrowserRouter>
    );
  }
}

export default App;
