import React, { Component } from "react";
import axios from 'axios';
const img = require('../../img/2.png');

class PopularShops extends Component {
  componentDidMount() {
    const token = localStorage.getItem('token');
    axios.post('/auth/verify', { token })
      .then(res => {
        if (res.data.success === false) {
          this.props.history.push('/login')
        }
      });
  }
  render() {
    return (
      <>
        <div className="shop">
          <img src={this.props.img} alt="" />
          <br />
          <div className="info">
            <span><h3>{this.props.shop.name}</h3></span>
            <h4>{this.props.shop.address}</h4>
            <h4>{this.props.shop.phone}</h4>
          </div>
        </div>
      </>
    );
  }
}

export default PopularShops;
