import React, { Component } from "react";
import reactGA from 'react-ga';
import axios from 'axios';
import Header from './Header';
import Navbar from './Navbar';
import Popular from './Popular';

let imgs = [];
const img1 = require('../../img/demo.jpg');
const img2 = require('../../img/demo2.jpg');
const img3 = require('../../img/demo3.jpg');
const img4 = require('../../img/demo4.jpg');
imgs = [img1, img2, img3, img4];

class AllProducts extends Component {
  state = {
    products: []
  }
  componentDidMount() {
    reactGA.initialize('UA-166832202-1');
    reactGA.pageview('/allproducts');
    const token = localStorage.getItem('token');
    if (!token)
      this.props.history.push('/login')
    axios.post('/auth/verify', { token })
      .then(res => {
        if (res.data.success === false) {
          this.props.history.push('/login')
        }
      });
    axios.get('/product/products')
      .then(res => {
        this.setState({ products: res.data.products });
      })
  }
  render() {

    return (
      <div>
        <Header />
        <div className="popular-container">
          {/* {this.state.products.map((top) => <Popular key={top._id} product={top} img={imgs[Math.floor(Math.random() * 4)]} />)} */}
          {this.state.products.map((top) => <Popular key={top._id} product={top} img={top.image ? `/uploads/${top.image}` : imgs[Math.floor(Math.random() * 4)]} />)}
        </div>
        <div className="clear"></div>
        <Navbar />
      </div>
    );
  }
}

export default AllProducts;
