import React, { Component } from "react";
import { Input, Button, notification } from 'antd';
import { Link, withRouter } from 'react-router-dom';
import axios from 'axios';
import reactGA from 'react-ga';

class Login extends Component {
  state = {
    email: '',
    password: '',
    type: 'client'
  }

  handleChange = (e) => {
    e.preventDefault();
    this.setState({ [e.target.name]: e.target.value })
  }

  handleLogin = (e) => {
    e.preventDefault();
    axios.post('/auth/login', {
      email: this.state.email,
      password: this.state.password,
      type: this.state.type
    })
      .then(res => {
        if (res.data.success) {
          localStorage.setItem('token', res.data.token);
          localStorage.setItem('user', JSON.stringify(res.data.user));
          let cart = {
            shopID: '',
            products: [],
            userID: res.data.user._id,
            to: res.data.user.address,
            from: ''
          }

          localStorage.setItem('cart', JSON.stringify(cart));


          if (res.data.user.type === 'admin') {
            this.props.history.push('/captain')
          } else {
            this.props.history.push('/')
          }
        } else {
          notification.open({
            message: 'Something went wrong',
            description: 'Please check your credentials.',
            onClick: () => {
              console.log('Notification Clicked!');
            },
          });
        }
      })
  }

  componentDidMount() {
    reactGA.initialize('UA-166832202-1');
    reactGA.pageview('/login');
    const token = localStorage.getItem('token');
    axios.post('/auth/verify', { token })
      .then(res => {
        if (res.data.success === true) {
          this.props.history.push('/')
        }
      });
  }

  render() {
    return (
      
        <div className="login-container">
          <div className="login">
              <h1>Login to LiqrCart.</h1>
              <Input id="slide" placeholder="Email or Phone" className="fadeInLeft" name="email" onChange={this.handleChange} />
              <br /> <br />
              <Input.Password placeholder="Password" name="password" onChange={this.handleChange} />
              <br /> <br />
            <Button type="primary" size="large" onClick={this.handleLogin}>Login</Button>
            <br /> <br />
            <p><Link to="/forgot">Forgot Password.</Link></p>
            
            <p><Link to="/signup">Not have an Account? Sign Up.</Link></p>        </div>
        </div>
    );
  }
}

export default withRouter(Login);
