import React, { Component } from "react";
import { Input, Typography } from 'antd';
import axios from 'axios';

class AddShop extends Component {

  state = {
    name: '',
    admin: '',
    email: '',
    phone: '',
    address: '',
    password: '',
    cpassword: '',
    error: []
  }



  render() {
    const handleChange = (e) => {
      this.setState({ [e.target.name]: e.target.value })
    }
    const handleSubmit = (e) => {
      e.preventDefault();
      const { name, admin, email, phone, address, password, cpassword } = this.state;
      if (name === '')
        this.setState({ error: [...this.state.error, ' Please Enter Shop Name'] });
      if (admin === '')
        this.setState({ error: [...this.state.error, ' Please Enter Owner Name'] });
      if (phone === '')
        this.setState({ error: [...this.state.error, ' Please Enter Phone'] });
      if (email === '')
        this.setState({ error: [...this.state.error, ' Please Enter email'] });
      if (address === '')
        this.setState({ error: [...this.state.error, 'Please Enter Shop Address'] });
      if (password === '')
        this.setState({ error: [...this.state.error, ' Please Enter password'] });
      if (cpassword === '')
        this.setState({ error: [...this.state.error, ' Please Enter Confirm Password'] });
      if (password !== cpassword)
        this.setState({ error: [...this.state.error, ' Passwords Do not match'] });

      axios
        .post("/shop/add", {
          name, admin, email, address, phone, password
        })
        .then(res => {
          if (res.data.success) {
            this.setState({ error: ['Shop Created.'] })
          } else {
            this.setState({ error: ['Shop could not be created'] })
          }
        })
        .catch(err => console.error(err));
    }

    return (
      <div>
        <div>
          {this.state.error.map(e => (
            <div key={e} ><Typography.Text type="danger">{e}</Typography.Text></div>
          ))}

        </div>
        <form onSubmit={handleSubmit}>
          <Input name="name" placeholder="Shop Name" onChange={handleChange} />
          <br /><br />
          <Input name="admin" placeholder="Owner" onChange={handleChange} />
          <br /><br />
          <Input name="email" placeholder="email" type="email" onChange={handleChange} />
          <br /><br />
          <Input name="phone" placeholder="phone" type="phone" onChange={handleChange} />
          <br /><br />
          <Input name="address" placeholder="address" type="text" onChange={handleChange} />
          <br /><br />
          <Input.Password name="password" placeholder="password" onChange={handleChange} />
          <br /><br />
          <Input.Password name="cpassword" placeholder="confirm password" onChange={handleChange} />
          <br /><br />
          <Input type="submit" value="Add" />
        </form>
      </div>
    );
  }
}

export default AddShop;