import React, { Component } from 'react'
import {Input, Button, notification} from 'antd';
import axios from 'axios';

export default class ForgotPassword extends Component {
  state = {
    step: 0,
    email: '',
    password: '',
    resetToken: ''
  }
  componentDidMount() {
    const token = localStorage.getItem('token');
    axios.post('/auth/verify', { token })
      .then(res => {
        if (res.data.success === true) {
          this.props.history.push('/')
        }
      });
  }
  showNotification = (desc) => {
    notification.open({
      title: 'Notification',
      description: desc
    });
  }
  handleChange = e => {
    this.setState({[e.target.name]: e.target.value});
  }
  handleForget = e => {
    if(!this.state.email){
      this.showNotification('Email cannot be empty');
      return;
    }
    this.showNotification('Please wait')
    axios.post('/auth/forgotPassword', {email: this.state.email})
    .then(res => {
      if(res.data.success) {
        this.setState({step:1, resetToken : res.data.token});
        this.showNotification('Check your email')
      } else {
        this.showNotification(res.data.err);
      }
    }).catch(err => {
      this.showNotification('Server Error. Please try again after some time.');
    });
  }
  handleReset = (e) => {
    if(!this.state.password) {
      this.showNotification('Password cannot be empty');
      return;
    }
    if(this.state.password.length < 7) {
      this.showNotification('Password length must be alteast 8.');
      return;
    }
    if(!this.state.resetToken) {
      this.showNotification('Reset token cannot be empty.');
      return;
    }
    axios.post('/auth/resetPassword', {token: this.state.resetToken, password: this.state.password})
    .then(res => {
      if(!res.data.success){
        this.showNotification(res.data.err);
      } else {
        this.props.history.push('/login');
      }
    }).catch(err => {
      this.showNotification('Something went wrong. Check Your Internet Conection');
    })
  }
  render() {
    return (
      <div>
        {
          this.state.step === 0 &&
          (<div className="email-input">
          ForgotPassword
           <Input type="email" placeholder="Enter your registered email address." name="email" onChange={this.handleChange} />
           <Button type="danger" onClick={this.handleForget}>Forget Password</Button>
          </div>) 
        } 
        {
          this.state.step === 1 &&
          (<div className="token-input">
          Reset Password
           <Input type="text" placeholder="Enter reset token." name="resetToken" onChange={this.handleChange} />
           <Input.Password placeholder="Enter new password." name="password" onChange={this.handleChange} />
           <Button type="danger" onClick={this.handleReset}>Reset Password</Button>
          </div>)
        }
      </div>
    )
  }
}
