import React from "react";
import {
  Layout,
  Typography,
  Modal,
  Input,
  Row,
  Button,
  notification,
} from "antd";
import Sidebar from "./Sidebar";
import axios from "axios";
const { Title } = Typography;
const { Header, Content, Footer } = Layout;

class Category extends React.Component {
  state = {
    collapsed: false,
    visible: false,
    newCategoryName: "",
    categories: [],
  };

  componentDidMount() {
    const token = localStorage.getItem("token");
    axios
      .post("/auth/verifyAdmin", { token })
      .then((res) => {
        if (!res.data.success) {
          this.props.history.push("/login");
        }
      })
      .catch((err) => {
        console.log(err);
        this.props.history.push("/login");
      });

    axios.get("/category/allCategories").then(({ data }) => {
      this.setState({ categories: data.categories });
    });
  }

  onCollapse = (collapsed) => {
    console.log(collapsed);
    this.setState({ collapsed });
  };
  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e) => {
    this.setState({
      visible: false,
    });
    axios
      .post("/category/add", {
        name: this.state.newCategoryName.trim().toLowerCase(),
      })
      .then(({ data }) => {
        if (data.success) {
          this.setState({
            categories: [data.category, ...this.state.categories],
          });
          this.openNotification("Category Created.");
        } else {
          this.openNotification("Category Could not be Created.");
        }
      });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleDelete = (id) => {
    axios.post("/category/delete", { catID: id }).then((res) => {
      if (res.data.success) {
        let newCats = this.state.categories.filter((cat) => {
          return cat._id !== id;
        });
        this.setState({ categories: newCats });
        this.openNotification("Category deleted.");
      } else {
        this.openNotification("Category Could not be deleted.");
      }
    });
  };

  handleChange = (e) => {
    this.setState({ newCategoryName: e.target.value });
  };

  openNotification = (desc) => {
    notification.open({
      message: "Notification",
      description: desc,
      onClick: () => {
        console.log("Notification Clicked!");
      },
    });
  };

  render() {
    const { categories } = this.state;
    return (
      <Layout style={{ minHeight: "100vh" }}>
        <Sidebar />
        <Layout>
          <Header
            style={{
              background: "#fff",
              padding: "6px",
              margin: "16px",
              textAlign: "center",
            }}
          >
            <Title level={2}>Category</Title>
          </Header>
          <Content style={{ margin: "0 16px" }}>
            <div style={{ padding: 24, background: "#fff", minHeight: 360 }}>
              <Row type="flex" justify="end">
                <Button
                  type="primary"
                  onClick={this.showModal}
                  style={{ marginRight: "12px", marginBottom: "16px" }}
                >
                  Add New Category
                </Button>
              </Row>
              {categories.map((cat) => (
                <Row
                  type="flex"
                  justify="space-between"
                  style={RowStyles}
                  key={cat._id}
                >
                  <Typography.Text>{cat.name}</Typography.Text>
                  <Button
                    type="danger"
                    size="small"
                    onClick={() => {
                      this.handleDelete(cat._id);
                    }}
                    style={{ marginLeft: "16px" }}
                  >
                    Delete
                  </Button>
                </Row>
              ))}
            </div>

            <div>
              <Modal
                title="Enter Category Name"
                visible={this.state.visible}
                onCancel={this.handleCancel}
                onOk={this.handleOk}
                okButtonText="ADD"
              >
                <Input
                  placeholder="Category Name"
                  onChange={this.handleChange}
                />
              </Modal>
            </div>
          </Content>
          <Footer style={{ textAlign: "center" }}>
            LiqrCart 2020 Created by Rinnegan Technologies
          </Footer>
        </Layout>
      </Layout>
    );
  }
}

export default Category;

const RowStyles = {
  border: "0.5px solid grey",
  borderRadius: "5px",
  margin: "12px",
  padding: "12px",
};
