import React from 'react'
import { Link } from 'react-router-dom'
import { Layout, Menu, Icon } from 'antd';

const { Sider } = Layout;

class Sidebar extends React.Component {
  state = {
    collapsed: true,
  };

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  render() {
    return (
      <Sider collapsible collapsed={this.state.collapsed} onCollapse={this.onCollapse}>
        <div className="logo" />
        <Menu theme="dark" mode="inline">
          <Menu.Item key="1">
            <Link to="/shops/orders">
              <Icon type="shopping-cart" />
              <span>Orders</span>
            </Link>
          </Menu.Item>

          <Menu.Item key="sub2">
            <span>
              <Link to="/shops/Stock">
                <Icon type="stock" />
                <span>Stock</span>
              </Link>
            </span>
          </Menu.Item>

          <Menu.Item
            key="sub4"
          >
            <Link to="/shops/product">
              <Icon type="plus" />
              <span>Add Product</span>
            </Link>
          </Menu.Item>

          <Menu.Item
            key="sub5"
          >
            <Link to="/logout">
              <Icon type="logout" />
              <span>Logout</span>
            </Link>
          </Menu.Item>
        </Menu>
      </Sider>

    );
  }
}

export default Sidebar;