import React, { Component } from "react";
import axios from "axios";
import { Dropdown, Affix } from "antd";
import { Link } from "react-router-dom";

const gridSvg = require("../../css/icons/grid-white.svg");
const searchSvg = require("../../css/icons/search-white.svg");
const homeSvg = require("../../css/icons/home-white.svg");
const cartSvg = require("../../css/icons/shopping-cart-white.svg");
const beerSvg = require("../../img/beer.svg");

class Navbar extends Component {
  state = {
    categories: [],
    menu: "",
  };
  componentDidMount() {
    axios.get("/category/allCategories").then(({ data }) => {
      this.setState({ categories: data.categories });
      let list = (
        <div>
          <ul className="cat-group">
            <h2 className="center">Categories</h2>
            {data.categories.map((cat) => (
              <Link key={cat._id} to={`/search/category#${cat._id}`}>
                {" "}
                <li className="cat-group-item">
                  <img src={beerSvg} alt="" />
                  <strong>{cat.name}</strong>
                </li>
              </Link>
            ))}
          </ul>
        </div>
      );
      this.setState({ menu: list });
    });
  }

  render() {
    return (
      <>
        <Affix offsetBottom={0}>
          <div className="navbar">
            <Link to="/">
              <div>
                <img src={homeSvg} alt="" />
              </div>
            </Link>
            <div>
              <Link to="/cart">
                <img src={cartSvg} alt="" />
              </Link>
            </div>
            <div>
              <Link to="/search">
                <img src={searchSvg} alt="" />
              </Link>
            </div>
            <Dropdown overlay={this.state.menu} placement="topLeft">
              <div>
                {" "}
                <img src={gridSvg} alt="" />
              </div>
            </Dropdown>
          </div>
        </Affix>
      </>
    );
  }
}

export default Navbar;
