import React, { Component } from "react";
import {  Typography } from 'antd';
import axios from 'axios';
import Header from './Header';
import Navbar from './Navbar';
import reactGA from 'react-ga';

// let imgs = [];
// const img1 = require('../../img/demo.jpg');
// const img2 = require('../../img/demo2.jpg');
// const img3 = require('../../img/demo3.jpg');
// const img4 = require('../../img/demo4.jpg');
// imgs = [img1, img2, img3, img4];

class AllBrands extends Component {
  state = {
    brands: []
  }
  componentDidMount() {
    reactGA.initialize('UA-166832202-1');
    reactGA.pageview('/allbrands');
    const token = localStorage.getItem('token');
    if (!token)
      this.props.history.push('/login')
    axios.post('/auth/verify', { token })
      .then(res => {
        if (res.data.success === false) {
          this.props.history.push('/login')
        }
      });
    axios.get('/brand/allBrands')
      .then(res => {
        this.setState({ brands: res.data.brands });
      })
  }
  render() {

    return (
      <div>
        <Header />
        <Typography.Title level={1} className="center">All Brands</Typography.Title>
        <div className="brands-container">
          {this.state.brands.map((top) => <div key={top._id} className="brand">
            <a href={`/allBrands/${top.name}`}><h2>{top.name}</h2></a>
          </div>)}
        </div>
        <div className="clear"></div>
        <Navbar />
      </div>
    );
  }
}

export default AllBrands;
