import React, { Component } from "react";
import { Carousel } from 'antd';
import axios from 'axios';
import Header from './Header';
import Navbar from './Navbar';
import TopCard from './TopCard';
import Popular from './Popular';
import PopularBrands from './PopularBrands';
import PopularShops from './PopularShops';
import reactGA from 'react-ga';
const p1 = '../../img/p1.jpg';
const p2 = '../../img/p2.jpg';
const p3 = '../../img/p3.jpg';
const p4 = '../../img/p4.jpg';
const imgs = [p1, p2, p3, p4];

class Home extends Component {
  state = {
    products: [],
    brands: [],
    shops: []
  }
  componentDidMount() {
    reactGA.initialize('UA-166832202-1');
    reactGA.pageview('/home');
    const token = localStorage.getItem('token');
    if (!token){
      this.props.history.push('/login');
    return;}
    axios.post('/auth/verify', { token })
      .then(res => {
        if (res.data.success === false) {
          this.props.history.push('/login')
        }
      });
    axios.get('/product/products')
      .then(res => {
        this.setState({ products: res.data.products });
      })

    axios.get('/brand/allBrands')
      .then(res => {
        this.setState({ brands: res.data.brands });
      })

    axios.get('/shop/allShops')
      .then(res => {
        this.setState({ shops: res.data.shops });
      })
  }
  render() {
    const Top = [...this.state.products].slice(0, 4);
    const popularProduct = [...this.state.products].slice(0, 4);
    const popularBrands = [...this.state.brands].slice(0, 4);
    const popularShops = [...this.state.shops]
    return (
      <div>
        <Header />
        <Carousel effect="fade" className="carousel" >
          {Top.map((top, index) => <TopCard key={index} draggable={true} product={top} img={imgs[index]} />)}
        </Carousel>
        <div className="center"><h2>Popular</h2></div>
        <div className="popular-container">
          {popularProduct.map((top) => <Popular key={top._id} product={top} img={top.image ? `/uploads/${top.image}` : imgs[Math.floor(Math.random() * 4)]} />)}
        </div>
        <div className="view-more"><a href="/allproducts"><button>View more</button></a></div>
        <div className="center"><h2>Popular Brands</h2></div>
        <div className="popular-container">
          {popularBrands.map((top) => <PopularBrands key={top._id} brand={top} img={top.image ? `/uploads/${top.image}` : imgs[Math.floor(Math.random() * 4)]}  />)}
        </div>
        <div className="view-more"><a href="/allbrands"><button>View more</button></a></div>
        <div className="center"><h2>All Shops</h2></div>
        <div className="shop-container">
          {popularShops.map((top) => <PopularShops key={top._id} shop={top} img={imgs[Math.floor(Math.random() * 4)]} />)}
        </div>
        <div className="clear"></div>
        <Navbar />
      </div>
    );
  }
}

export default Home;