import React, { Component } from "react";
import {  Input } from 'antd';
import axios from 'axios';
import { Link } from 'react-router-dom'
import Header from './Header';
import Navbar from './Navbar';

const beerSvg = require('../../img/beer.svg')

class SearchCategory extends Component {
  state = {
    products: [],
    results: []
  }
  componentDidMount() {
    const token = localStorage.getItem('token');
    if (!token)
      this.props.history.push('/login')
    axios.post('/auth/verify', { token })
      .then(res => {
        if (res.data.success === false) {
          this.props.history.push('/login')
        }
      });

    axios.post('/product/productsByCategory', { category: this.props.location.hash.split('#')[1] })
      .then(res => {
        this.setState({ products: res.data.products })
      })
  }



  render() {

    return (
      <div>
        <Header />
        <Input.Search
          placeholder="input search text"
          size="large"
          onSearch={this.handleSearch}
          className="search-box"
        />
        {this.state.results.map(r => <Link to={`/getproduct/${r._id}`} key={r._id}> <div className="search-result" >
          <img src={beerSvg} alt="" />
          <p>{r.name}</p>
        </div></Link>)
        }
        {this.state.products.map(r => <Link to={`/getproduct/${r._id}`} key={r._id}> <div className="search-result" >
          <img src={beerSvg} alt="" />
          <p>{r.name}</p>
        </div></Link>)
        }
        <Navbar />
      </div >
    );
  }
}

export default SearchCategory;
